  height: 3.25em;
  /* box-shadow: 0 10px 29px 0 lightgray; */
  margin-left: 0.75em;
  border-bottom: 5px solid #2D383A;
  border-top: none;
  border-left: none;
  border-right: none;
  z-index: 999999;
  box-shadow: none;
  /* border-radius: 25px; */
}

.searchfilter:hover {
}

div.searchstrip input[type=text] {
  width: 27em;
  background-color: white;
  border: 2px solid #2D383A;
  height: 3em;
  font-size: large;
  border-radius: 0px;
  padding: 0 0 0 15px;
  /* box-shadow: 0 10px 29px 0 lightgray; */
  margin-right: 1em;
  overflow: hidden;
}

div.searchstrip input[type=text]:hover {
  width: 27em;
}

div.searchstrip input[type=text]:focus {
  width: 27em;
}

div.searchstrip h1 {
  font-size: 3em;
}

.centeritem {
  text-align: center;
  overflow: hidden;
}

.centereditem {
  display: inline-block;
  overflow: hidden;
}

.centeredtopbaritem {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  overflow: hidden;
}

.flexsplit {
  display: flex;
  flex-direction: row;
  margin: 0em 3em 0em 3em;
}

.clearfieldbutton {
  margin-right: 5em;
  /* margin-top: -5px; */
  box-shadow: none;
  font-size: large;
  border: none;
  transition: 0s;
  border: 2px solid white;
  border-radius: 50px;
  transition: 0.1s;
}

.clearfieldbutton:hover {
  border: 2px solid black;
  border-radius: 50px;
}

.searchflexsplit {
  display: flex;
  flex-direction: row;
  text-align: left;
  /* margin-left: 30px; */
  padding: 1em 2em 2em 0em;
  /* width: 60em; */
  overflow: hidden;
  /* justify-content: space-around; */
}

.moreflexsplit {
  display: flex;
  flex-direction: row;
  margin: 1em 0em 1em 3em;
  padding-right: 0em;
  width: 50%;
  border-left: 1px solid #2D383A;
  padding: 0em 0 0em 3em;
  text-align: left;
}

.leftsplit {
  width: 40%;
}

.rightsplit {
  line-height: 200%;
  
  text-align: center;
}

.farrightbutton {
  padding: 2.65em 0 2em 2em;
  line-height: 100%;
}

.smallcopybutton {
  font-size: medium;
  border-radius: 50px;
  cursor: pointer;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.smallcopybutton:hover {
  cursor: pointer;
  background-color: whitesmoke;
}

div.streamlinedlargecontent {
  margin-top: 1em;
  margin-right: 5em;
  width: 42em;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  vertical-align: middle;
  text-align: center;
  font-size: medium;
  flex: auto;
  background-color: whitesmoke;
  /* border: 1px solid #2D383A; */
  
  box-shadow: 5px 5px #2D383A;
  border-radius: 25px;
}

.mobilestreamlinedlargecontent {
  margin-top: 1em;
  margin-left: 0;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  vertical-align: middle;
  text-align: center;
  font-size: medium;
  flex: auto;
  background-color: whitesmoke;
  /* border: 1px solid #2D383A; */
  
  box-shadow: 5px 5px #2D383A;
  border-radius: 25px;
}

.mobilestreamlinedlargecontent p {
  margin: 0 2em 0 2em;
}

.textarea {
  border: 2px solid #5538C8;
  padding: 1em 1em 1em 1em;
  border-radius: 0px;
  font-family: 'Cairo', sans-serif;
}

.inputtypebutton {
  border: 2px solid #5538C8;
  color: #5538C8;
  font-size: medium;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px 15px 10px 15px;
  height: 50px;
  background-color: white;
  transition: 0.3s;
  outline: none;
}

.inputtypebutton:hover {
  background-color: #5538C8;
  color: white;
}

.instructionguide {
  text-align: center;
  
}

.instructionguide p {
  font-size: medium;  
  margin-top: 1em;
  margin-bottom: 1em;
  display: inline-block;
  max-width: 500px;
}

.instructionguide img {
  width: 500px;
}

.instructionguide h2 {
  display: block;
  margin: 10px auto 10px auto;
  border-bottom: 4px solid #5538C8;
  min-width: 20px;
  padding: 20px 100px 10px 100px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: large;
  color: #5538C8;
}

.upload-btn-wrapper {
  position: relative;
  overflow: hidden;
  display: inline-block;
}

.btn {
  border: 2px solid #5538C8;
  color: #5538C8;
  background-color: white;
  padding: 8px 20px;
  border-radius: 8px;
  font-size: 20px;
  font-weight: bold;
}

.upload-btn-wrapper input[type=file] {
  font-size: 100px;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}

.hidetext {
  font-size: 0;
}

.joinroom {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0.7em 1.3em 0.7em 1.3em;
  border-radius: 0px;
  transition: 0.5s;
  border: 2px solid #5538C8;
  font-size: large;
  margin: 0;
}

.joinroom:hover {
  background-color: white;
  color: #5538C8;
}

.roomfield {
  font-size: large;
}

.roompage {
  margin: 3em 3em 0em 5em;
  height: 100%;
  /* background-image: url('wallpaper.png'); */
  background-size: 100%;
  background-position: center bottom;
  background-repeat: no-repeat;
}

.roomdescr {
  
}

.warning {
  font-size: medium;
  padding: 0.65em 1em 0.65em 1em;
  border-radius: 0px;
  /* border: 2px solid red; */
  color: red;
  margin: 0 0 10px 2em;
}

.returnbutton {
  font-size: medium;
  padding: 0.65em 1em 0.65em 1em;
  border: 2px solid #5538C8;
  color: white;
  background-color: #5538C8;
  border-radius: 10px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0 0 10px 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  transition: 0.5s;
}

.returnbutton:hover {
  border: 2px solid #5538C8;
  color: #5538C8;
  background-color: white;
}

.leftsideofheader {
  text-align: left;
  margin-left: 15%;
}

.rightsideofheader {
  text-align: right;
  margin-right: 18%;
  width: 20em;
}

.introhead {
  margin: 0 0 0 0;
}

#headerlarge {
  margin: 1.5em 0 0.4em 0;
}

#headersmallparagraph {
  line-height: 1.2em;
  font-family: 'Cairo';
  font-size: 1.5em;
  margin: 1.7em 0 0 0;
}

.accountsettingsbutton {
  /* background-color: white; */
  color: white;
  border-radius: 25px;
  /* margin-left: -7em; */
  font-size: large;
  font-family: 'Cairo';
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: left;
  /* padding: 0.4em 1.4em 0.4em 1.4em; */
}

#removeformat {
  margin: 0 0 0 0;
  padding: 0 0 0 0;
  text-align: left;
  cursor: pointer;
}

#removeformat:hover {
  text-decoration: underline;
  color: white;
}

.roundunenrollbutton {
  background-color: white;
  border-radius: 25px;
  font-size: large;
  margin-bottom: 2em;
  font-family: 'Cairo';
  padding: 0.2em 1.5em 0.2em 1.5em;
}

.parallelshift {
  margin-left: 15%;
  margin-right: 15%;
}

.overviewheader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  
}

.mobileoverviewheader {
  text-align: center;
}

.mobileoverviewheader p {
  font-size: 2em;
  font-family: 'Raleway';
  color: white;
  margin: 2em 0 0 0;
}

.overviewheader h1 {
  font-size: 3em;
  margin-bottom: 0.3em;
}

.overviewheader p {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1.5em;
  color: white;
}

.discordbutton {
  color: #2D383A;
  font-size: large;
  border-radius: 50px;
  background-color: white;
  /* font-family: "Raleway"; */
  box-shadow: 5px 5px #2D383A;
  padding: 0.4em 1.4em 0.4em 1.4em;
  transition: 0.5s;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-top: 2.25em;
  margin-bottom: 2em;
  margin-right: 5px;
  float: right;
}

.discordbutton:hover {
  box-shadow: 1px 1px #2D383A;
}

.mobilediscordbutton {
  color: #2D383A;
  font-size: large;
  border-radius: 50px;
  background-color: white;
  /* font-family: "Raleway"; */
  box-shadow: 5px 5px #2D383A;
  padding: 0.5em 1em 0.5em 1em;
  transition: 0.5s;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 2em;
  margin-right: auto;
  margin-left: auto;
}

.courselist h1 {
  margin-left: 14.5%;
  margin-top: 2em;
  font-family: 'Raleway';
  font-size: 3em;
}

